import React, { useState, useEffect } from 'react';
import _ from 'lodash';
import { useTheme } from '@mui/material/styles';

import { Unstable_Grid2 as Grid } from '@mui/material';

import { CardImg } from './Img';
import { CardDetails } from './CardDetails';
import { SelectDeckQuantity } from './CustomRating';

const getDifferentNumbersWithSameId = (deck, cardEl, sameIdCount) => {

  //操作したIDと同IDがなければ空配列
  if (!sameIdCount) return [];

  //操作したIDと同IDで異番号のカードの全ての番号s
  const differentNumbersWithSameId = deck.filter(card => card.id === cardEl.id && card.number !== cardEl.number).map(card => card.number);

  //若い番号から削除対象にするためソート
  differentNumbersWithSameId.sort();

  return differentNumbersWithSameId;
};

export const CardOfList = ({ displayedCardList, deckContents, setDeckContents, viewWidth }) => {
  const [selectedCard, setSelectedCard] = useState(null);
  const [open, setOpen] = useState(false);






  const handleRatingChange = (cardEl, newValue) => {
    setDeckContents(prevState => {
      const newDeckContents = _.cloneDeep(prevState);

      if (cardEl.type === 'partner' || cardEl.type === 'case') {
        newDeckContents[cardEl.type] = newValue ? cardEl : null;
        return newDeckContents;
      }

      let deck = newDeckContents.deck;

      //デッキに既にある同IDカードの枚数
      const sameIdCount = deck.filter(card => card.id === cardEl.id).length;
      //デッキに既にある異IDの同番号カードの番号s(若い番号から整列済)
      let differentNumbersWithSameId = getDifferentNumbersWithSameId(deck, cardEl, sameIdCount);

      //一旦、操作したカードを除く
      deck = deck.filter(card => !(card.id === cardEl.id && card.number === cardEl.number));
      //操作したカードを追加する
      for (let i = 0; i < newValue; i++) {
        deck.push(cardEl);
      }

      //操作したカード追加後の、同IDカードs
      let sameIdCards = deck.filter(card => card.id === cardEl.id);

      //同IDカード枚数上限は3なので、追加して超えた分を削除する
      if (sameIdCards.length > 3) {
        while (sameIdCards.length > 3) {

          //デッキから、differentNumbersWithSameId[0]のカード番号を持つ要素を削除
          const index = deck.findIndex(card => card.number === differentNumbersWithSameId[0]);
          if (index !== -1) {
            deck.splice(index, 1);
          }

          //関係する配列を更新し繰り返す
          differentNumbersWithSameId.shift();
          sameIdCards = deck.filter(card => card.id === cardEl.id);
        }
      }

      deck.sort((a, b) => a.number.localeCompare(b.number));
      newDeckContents.deck = deck;

      return newDeckContents;
    });
  };


  const theme = useTheme();
  const spacing = parseInt(theme.spacing(1), 10); // Gridのspacing
  const padding = parseInt(theme.spacing(1), 10); // padding
  const minWidth = 200; // カードの最小幅

  // カードが配置される実際のスペースを計算
  const actualSpace = Math.floor(viewWidth) - padding ;

  // 1行に表示できるカードの最大数を計算
  const maxCardsPerRow = Math.floor(actualSpace / minWidth);

  const totalSpacing = spacing * maxCardsPerRow

  // カード1枚あたりのスペースを計算
  const cardSize = Math.floor((actualSpace - totalSpacing) / (maxCardsPerRow));

  return (
    <>
      <Grid container spacing={1} sx={{ padding: 1, paddingRight: 0, width: viewWidth }} >
        {displayedCardList.map(cardEl => (
          <Grid key={cardEl.number} >
            <div
              onClick={() => {
                setSelectedCard(cardEl);
                setOpen(true);
              }}
              style={{ cursor: 'pointer' }}
            >
              <CardImg cardEl={cardEl} size={cardSize * 1.4} />
            </div>
            <div
              style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                fontSize: 14,
              }}>
              {cardEl.number}
              <SelectDeckQuantity
                name={`card-rating-${cardEl.number}`}
                value={(cardEl.type === 'partner' || cardEl.type === 'case')
                  ? (deckContents[cardEl.type]?.number === cardEl.number ? 1 : 0)
                  : deckContents.deck.filter(card => card.number === cardEl.number).length
                }
                max={(cardEl.type === 'partner' || cardEl.type === 'case')
                  ? 1
                  : 3
                }
                onChange={(event, newValue) => handleRatingChange(cardEl, newValue)}
              />
            </div>
          </Grid>
        ))}
      </Grid>

      {selectedCard &&
        <CardDetails
          open={open}
          displayedCardList={displayedCardList}
          selectedCard={selectedCard}
          setSelectedCard={setSelectedCard}
          handleClose={() => {
            setOpen(false)
          }}
        />
      }
    </>
  );
};