import React from 'react';
import { Button, Dialog, DialogTitle, DialogContent, IconButton } from '@mui/material';

import { darken, lighten } from '@mui/system';

import DeleteIcon from '@mui/icons-material/Delete';
import PersonRemoveIcon from '@mui/icons-material/PersonRemove';
import SaveIcon from '@mui/icons-material/Save';
import FolderOpenIcon from '@mui/icons-material/FolderOpen'; // または GetAppIcon
import GetAppIcon from '@mui/icons-material/GetApp';
import InfoIcon from '@mui/icons-material/Info';
import WarningIcon from '@mui/icons-material/Warning';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import ArrowDropUpIcon from '@mui/icons-material/ArrowDropUp';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import CheckBoxOutlineBlankIcon from '@mui/icons-material/CheckBoxOutlineBlank';
import CheckBoxIcon from '@mui/icons-material/CheckBox';
import AddIcon from '@mui/icons-material/Add';
import PrevIcon from '@mui/icons-material/NavigateBefore';
import NextIcon from '@mui/icons-material/NavigateNext';
import CloseIcon from '@mui/icons-material/Close';
import ViewListIcon from '@mui/icons-material/ViewList';
import SaveAltIcon from '@mui/icons-material/SaveAlt';

export const CommonButton = ({ onClick, width = '100%', height, bgColor = '#ffffff', children }) => {
  return (
    <Button
      aria-label="delete"
      color="primary"
      onClick={onClick}
      sx={{
        width: width, // 幅を設定します
        height: height ?? 36, // 高さを設定します
        color: '#ffffff',
        bgcolor: darken(bgColor, 0.5),
        boxShadow: 1,
        '&:hover': {
          bgcolor: darken(bgColor, 0.4),
          boxShadow: 3,
        }
      }}
    >
      <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', width: '100%', lineHeight: 1.2 }}>
        {children}
      </div>
    </Button>
  )
}

export const CloseIconButton = ({ onClick, sx, width }) => {
  return (
    <IconButton
      sx={{
        ...sx,
        width,
        height: width,
        backgroundColor: darken('#ffff00', 0.5),
        '&:hover': {
          backgroundColor: darken('#ffff00', 0.3),
        },
        boxShadow: '0px 0px 5px 5px rgba(0,0,0,0.75)', // ここで影を追加

      }}
      onClick={onClick}
    >
      <CloseIcon />
    </IconButton>
  );
};

