import React, { useState, useEffect } from 'react';
import {  BrowserRouter as Router, Route, Link, Routes } from "react-router-dom";
import { Button, Tab, Tabs, Unstable_Grid2 as Grid } from '@mui/material';

import { CardListTop } from './CardListTop';
import { DeckListTop } from './DeckListTop';
import { HideOnScrollAppBar } from './CustomAppBar';


const Card = ({ setTabValue }) => {
  useEffect(() => {
    setTabValue(0);
  }, [setTabValue]);
  return <CardListTop />;
};

const Deck = ({ setTabValue }) => {
  useEffect(() => {
    setTabValue(1);
  }, [setTabValue]);
  return (
    <Button variant="contained" color="primary" component={Link} to="/cardlist">
  カードリストへ
</Button>

  );
};

const OnePersonTool = ({ setTabValue }) => {
  useEffect(() => {
    setTabValue(2);
  }, [setTabValue]);
  return <div>シミュレータ</div>;
};


export const Top = () => {
  const [value, setValue] = useState(0)

  return (
    <Router basename="/conan-cardgame"> {/* ベースURLを設定 */}
      <HideOnScrollAppBar>
        <Grid container>
          <Grid>
            <Tabs value={value} onChange={(event, newValue) => setValue(newValue)}>
              <Tab label="カード" component={Link} to="/cardlist" />
              {/* <Tab label="デッキ" component={Link} to="/decklist" />
              <Tab label="シミュレータ" component={Link} to="/simulator" /> */}
            </Tabs>
          </Grid>
        </Grid>
      </HideOnScrollAppBar>
      <Routes>
        <Route path="/" element={<Card setTabValue={setValue} />} />
        <Route path="/cardlist" element={<Card setTabValue={setValue} />} />
        <Route path="/decklist" element={<Deck setTabValue={setValue}/>} />
        <Route path="/simulator" element={<OnePersonTool setTabValue={setValue} />} />
      </Routes>
    </Router>
  );
};
