import React from 'react';
import { darken, lighten } from '@mui/system';



export const CardImg = ({ cardEl, size , boxShadow }) => {

  return (
    <img
      src={cardEl.number
        ? `${process.env.PUBLIC_URL}/${cardEl.number}.jpg`
        : 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'
      }
      alt=''
      style={{
        width: cardEl.type === 'case' ? size : size / 1.4,
        height: cardEl.type === 'case' ? size / 1.4 : size,
        borderRadius: `${size / 25}px`,
        verticalAlign: 'bottom',
        boxShadow,

      }}
    />
  );
};