import React, { useState, useEffect } from 'react';
import { Rating } from '@mui/material';

import { darken, lighten } from '@mui/system';
import AddBoxIcon from '@mui/icons-material/AddBox';
import AddBoxOutlinedIcon from '@mui/icons-material/AddBoxOutlined';

export const CustomRating = ({ name, value, onChange, max }) => {
  let icon, emptyIcon;

  if (name === 'graceLight') {
    icon = <AddBoxIcon fontSize="inherit" />;
    emptyIcon = <AddBoxOutlinedIcon fontSize="inherit" />;
  }


  return (
    <Rating
      name={name}
      value={value}
      onChange={onChange}
      max={max}
      icon={icon}
      emptyIcon={emptyIcon}
    />
  );
};


export const SelectDeckQuantity = ({ name, value, max, onChange }) => {
  return (
    <Rating
      name={name}
      value={value}
      max={max}
      onChange={onChange}
      icon={<AddBoxIcon fontSize="inherit" />}
      emptyIcon={<AddBoxOutlinedIcon fontSize="inherit" />}
    />
  );
};
