const Sorts = ({ sortField, sortOrder, handleSortField, toggleSortOrder }) => {
  return (
    <Grid container spacing={1}>
      <Grid>
        <IconButton
          onClick={toggleSortOrder}
          sx={{
            width: '36px',
            height: '36px',
            color: '#000000',
            bgcolor: darken('#ffffff', 0.2),
            boxShadow: 1,
            '&:hover': {
              bgcolor: darken('#ffffff', 0.15),
              boxShadow: 3,
            }
          }}
        >
          {sortOrder === 'asc' ? '▲' : '▼'}
        </IconButton>
      </Grid>
      <Grid>
        <CommonToggleButtonGroup
          exclusive={true}
          value={sortField}
          onChange={handleSortField}
          options={['number', 'id']}
        />
      </Grid>

    </Grid>
  );
};

const Filters = ({ filterEls }) => {
  return (
    <Grid container spacing={1}>
      {filterEls.map((filterEl, index) => (
        <Grid key={index} sx={{ display: 'flex', alignItems: 'center' }}>
          <div style={{ fontSize: 14, whiteSpace: 'nowrap' }}>
            {filterEl.label + '：'}
          </div>
          <CommonToggleButtonGroup
            value={filterEl.value}
            onChange={filterEl.onChange}
            options={filterEl.options}
          />
        </Grid>
      ))}
    </Grid>
  );
};

export const SortAndFilter = ({ sortField, setSortField, sortOrder, setSortOrder, filterText, setFilterText, filterProduct, setFilterProduct, filterColor, setFilterColor, filterType, setFilterType, filterRarity, setFilterRarity }) => {
  return (
    <Grid container sx={{ margin: 1, gap: 1 }}>

      <Grid
        sx={{
          bgcolor: '#000',
          padding: 1,
        }}
      >
        <Typography sx={{ fontSize: '14px', lineHeight: 1, fontWeight: 'bold' }}>ソート</Typography>
        <Sorts
          sortField={sortField}
          sortOrder={sortOrder}
          handleSortField={(event, newSortField) => {
            if (newSortField !== null) {
              setSortField(newSortField);
            }
          }}
          toggleSortOrder={() => {
            setSortOrder(prevSortOrder => prevSortOrder === 'asc' ? 'desc' : 'asc');
          }}
        />
      </Grid>

      <Grid
        sx={{
          bgcolor: '#000',
          padding: 1,
        }}
      >
        <Typography sx={{ fontSize: '14px', lineHeight: 1.2, fontWeight: 'bold' }}>フィルター</Typography>

        <Grid container spacing={1}>
          <Grid sx={{ width: 400 }}>
            <SearchBox
              value={filterText}
              onChange={(e) => {
                setFilterText(e.target.value);
              }}
            />
          </Grid>

          <Grid sx={{ width: 400 }}>
            <CommonSelectMenu
              label="商品"
              value={filterProduct}
              onChange={(event) => {
                setFilterProduct(event?.target.value || '');
              }}
              options={[
                'CT-P01 Case-Booster 01 探偵たちの切札',
                'CT-D01 Case-StartDeck 01 江戸川コナン',
                'CT-D02 Case-StartDeck 02 服部平次',
                'CT-D03 Case-StartDeck 03 怪盗キッド',
                'CT-D04 Case-StartDeck 04 赤井秀一',
                'CT-D05 Case-StartDeck 05 安室透',
              ]}
            />
          </Grid>

          <Grid>
            <Filters
              filterEls={[
                {
                  label: '色',
                  value: filterColor,
                  onChange: (e, newFilterColor) => {
                    setFilterColor(newFilterColor);
                  },
                  options: ['青', '緑', '白', '赤', '黄', '黒'],
                },
                {
                  label: '種類',
                  value: filterType,
                  onChange: (e, newFilterType) => {
                    setFilterType(newFilterType);
                  },
                  options: ['partner', 'character', 'event', 'case'],
                },
                {
                  label: 'レア',
                  value: filterRarity,
                  onChange: (event, newFilterRarity) => {
                    setFilterRarity(newFilterRarity);
                  },
                  options: ['C', 'CP', 'R', 'RP', 'SR', 'SRP', 'D', 'PR'],
                },
              ]}
            />
          </Grid>

        </Grid>
      </Grid>
    </Grid>
  );
};


// デッキのカードの編集を行う関数
const editDeck = (prevState, cardEl) => {
  const newDeckContents = _.cloneDeep(prevState);
  const deck = newDeckContents.deck;

  if (cardEl.type === 'partner' || cardEl.type === 'case') {
    newDeckContents[cardEl.type] = null;
  } else if (cardEl.type === 'character' || cardEl.type === 'event') {
    const index = deck.findIndex(card => card.id === cardEl.id);
    if (index !== -1) {
      deck.splice(index, 1);
    }
  }

  deck.sort((a, b) => a.number.localeCompare(b.number));

  return newDeckContents;
};

// カードを表示するコンポーネント
const CardOfDeck = ({ cardEl, onClick, size, type }) => {

  if (cardEl) {
    return (
      <div
        onClick={() => onClick(cardEl)}
        style={{ cursor: 'pointer' }}
      >
        <CardImg cardEl={cardEl} size={size} />
      </div>
    )
  }

  return (
    <div
      style={{
        border: '1px solid gray',
        borderRadius: size / 25,
        width: type === 'case' ? size : size / 1.4,
        height: type === 'case' ? size / 1.4 : size,
      }}
    />

  )




};

const EditingDeck = ({ deckList, setDeckList, selectedDeckIndex, setSelectedDeckIndex, deckContents, setDeckContents, viewWidth }) => {

  //カードサイズ
  const padding = 10
  const containerRef = useRef(null);
  const windowWidth = useWindowWidth(); // ウィンドウの幅を取得
  // const viewWidth = Math.min(windowWidth, 960) - padding * 2;
  const cardSize = Math.min((viewWidth - padding * 2) / 10 * 1.4);

  //カード編集
  const handleCardClick = (cardEl) => {
    setDeckContents(prevState => editDeck(prevState, cardEl));
  };

  //デッキ名編集
  const handleDeckNameChange = (event) => {
    const newDeckName = event.target.value;
    setDeckContents(prevState => {
      const newDeckContents = _.cloneDeep(prevState);
      newDeckContents.deckName = newDeckName;
      return newDeckContents;
    });
  };

  // ドロワーの開閉状態を管理するための状態変数
  const [drawerOpen, setDrawerOpen] = useState(false);

  // ドロワーを開く関数
  const handleDrawerOpen = () => {
    // 現在のdeckContentsをディープコピーし、それをdeckListに保存
    const newDeckContents = _.cloneDeep(deckContents);
    const newDeckList = [...deckList];
    newDeckList[selectedDeckIndex] = newDeckContents;
    setDeckList(newDeckList);

    setDrawerOpen(true);
  };

  // ドロワーを閉じる関数
  const handleDrawerClose = () => {
    setDrawerOpen(false);
  };

  // デッキを選択する関数（ドロワーを閉じる処理を追加）
  const handleDeckSelect = (index) => {
    setSelectedDeckIndex(index);
    setDeckContents(deckList[index]);
    handleDrawerClose();
  };

  // 新しいデッキを追加する一般的な関数
  const handleAddDeck = (newDeck) => {
    const newDeckList = [...deckList];
    newDeckList.push(newDeck);
    setDeckList(newDeckList);
    setSelectedDeckIndex(deckList.length);
    setDeckContents(newDeck);
  };

  // 新しいデッキを作成する関数
  const handleNewDeck = () => {
    const newDeck = { deckName: '空デッキ', partner: null, case: null, deck: [] };
    handleAddDeck(newDeck);
  };

  // 現在のデッキをコピーして新規追加する関数
  const handleCopyDeck = () => {
    const copiedDeck = _.cloneDeep(deckContents);
    copiedDeck.deckName = `${copiedDeck.deckName}(copy)`;
    handleAddDeck(copiedDeck);
  };

  // スターターデッキ1を追加する関数
  const handleAddStarterDeck1 = () => {
    const starterDeck1 = { deckName: `Deck ${deckList.length}`, partner: null, case: null, deck: [] }; // 仮
    handleAddDeck(starterDeck1);
  };

  // デッキを削除する関数
  const handleDeleteDeck = (event, index) => {
    event.stopPropagation(); // 追加：イベントの伝播を停止

    if (window.confirm('削除しますか？')) {
      const newDeckList = [...deckList];
      newDeckList.splice(index, 1);
      setDeckList(newDeckList);
      if (index === selectedDeckIndex) {
        setSelectedDeckIndex(0);
        setDeckContents(newDeckList[0]);
      } else if (index < selectedDeckIndex) {
        setSelectedDeckIndex(selectedDeckIndex - 1);
      }
    }
  };


  const handleSaveImage = () => {
    if (containerRef.current) {
      const scale = 1920 / viewWidth; // Adjust the scale to achieve a width of 1920 pixels
      domtoimage.toPng(containerRef.current, { style: { transform: `scale(${scale})`, transformOrigin: 'top left' }, width: windowWidth * scale, height: containerRef.current.clientHeight * scale })
        .then((dataUrl) => {
          const link = document.createElement('a');
          link.download = 'my-deck.png';
          link.href = dataUrl;
          link.click();
        })
        .catch((error) => {
          console.error('oops, something went wrong!', error);
        });
    }
  };



  return (
    <div style={{ padding, width: viewWidth }} ref={containerRef} >
      <Grid container sx={{ marginBottom: 1 }}>

        {/* デッキ選択のボタンとドロワー */}
        <Grid sx={{ width: '100%', display: 'flex', gap: 0.5 }}>
          <DeckDrawerButton onClick={handleDrawerOpen} />
          <Drawer anchor="right" open={drawerOpen} onClose={handleDrawerClose} PaperProps={{ style: { maxWidth: '400px' } }}>
            <Grid container spacing={1} sx={{ padding: 1 }} >
              {deckList.map((deck, index) => (
                <Grid xs={12} key={index}
                  sx={{
                    display: 'flex',
                    gap: 0.5,
                  }}
                >
                  <CommonButton onClick={() => handleDeckSelect(index)}>
                    <div>{deck.deckName}</div>
                    <div>{index === selectedDeckIndex && (<CheckIcon />)}</div>
                  </CommonButton>
                  <DeleteButton
                    onClick={(event) => {
                      event.stopPropagation();
                      handleDeleteDeck(event, index);
                    }}
                    width={36}
                    height={36}
                  />
                </Grid>

              ))}
              <Grid sx={{ width: '100%', marginTop: '44px' }}>
                <CommonButton onClick={handleNewDeck}>空デッキを新規追加</CommonButton>
              </Grid>
              <Grid sx={{ width: '100%' }}>
                <CommonButton onClick={handleCopyDeck}>現デッキをコピーして新規追加</CommonButton>
              </Grid>
              {/* <Grid sx={{ width: '100%' }}>
                <CommonButton onClick={handleAddStarterDeck1}>スターターデッキ1を新規追加</CommonButton>
              </Grid> */}
            </Grid>
          </Drawer>

          <DeckNameEdit
            value={deckContents.deckName}
            onChange={handleDeckNameChange}
          />
          <SaveAltButton onClick={handleSaveImage} />
        </Grid>

      </Grid>

      <div style={{ display: 'flex', height: cardSize }}>
        {deckContents.partner
          ? <CardOfDeck
            cardEl={deckContents.partner}
            onClick={handleCardClick}
            size={cardSize}
          />
          : <CardOfDeck
            size={cardSize}
          />
        }
        {deckContents.case
          ? <CardOfDeck
            cardEl={deckContents.case}
            onClick={handleCardClick}
            size={cardSize}
          />
          : <CardOfDeck
            size={cardSize}
            type={'case'}
          />
        }
        <div style={{ marginLeft: 'auto' }}>
          {deckContents.deck.length + '/40枚'}
        </div>
      </div>
      <div style={{ display: 'flex', flexWrap: 'wrap' }}>
        {deckContents.deck.map((cardEl, index) => (
          <CardOfDeck key={cardEl.number + index}
            cardEl={cardEl}
            onClick={handleCardClick}
            size={cardSize}
          />
        ))}
        {deckContents.deck.length < 40 && Array(40 - deckContents.deck.length).fill().map((_, index) => (
          <CardOfDeck key={index}
            size={cardSize}
          />
        ))}
      </div>

    </div>
  );
};


export const CardListTop = () => {

  const [isEditingDeck, setIsEditingDeck] = useState(false);


  const [sortField, setSortField] = useState('number');
  const [sortOrder, setSortOrder] = useState('asc');
  const [filterText, setFilterText] = useState('');
  const [filterProduct, setFilterProduct] = useState('');
  const [filterColor, setFilterColor] = useState([]);
  const [filterType, setFilterType] = useState([]);
  const [filterRarity, setFilterRarity] = useState([]);

  const displayedCardList = useMemo(() => {

    const filteredCardList = cardList.filter(card => {
      if (!card.name) return false;

      if (filterText
        && card.number !== filterText
        && card.id !== filterText
        && !card.name.includes(filterText)
        && !card.abilities.some(ability => ability.includes(filterText))
        && !card.cutIn.includes(filterText)
        && !card.inspiration.includes(filterText)
      ) {
        return false;
      }

      if (filterProduct && card.product !== filterProduct) return false;
      if (filterColor.length > 0 && !filterColor.some(color => card.colors.includes(color))) return false;
      if (filterType.length > 0 && !filterType.includes(card.type)) return false;
      if (filterRarity.length > 0 && !filterRarity.includes(card.rarity)) return false;
      return true;
    });

    const sortedCardList = [...filteredCardList].sort((a, b) => {
      if (a[sortField] < b[sortField]) return sortOrder === 'asc' ? -1 : 1;
      if (a[sortField] > b[sortField]) return sortOrder === 'asc' ? 1 : -1;
      return 0;
    });

    return sortedCardList;
  }, [sortField, sortOrder, filterText, filterColor, filterType, filterRarity, filterProduct]);



  const [deckList, setDeckList] = useState(() => {
    const savedDeckList = localStorage.getItem('conan-cardgame-deckList');
    return savedDeckList
      ? JSON.parse(savedDeckList)
      : [{ deckName: 'デッキ名', partner: null, case: null, deck: [] }];
  });

  // selectedDeckIndexの初期値をローカルストレージから読み込む
  const [selectedDeckIndex, setSelectedDeckIndex] = useState(() => {
    const savedIndex = localStorage.getItem('conan-cardgame-selectedDeckIndex');
    return savedIndex ? Number(savedIndex) : 0;
  });

  // 選択されたデッキの情報を管理するstate
  const [deckContents, setDeckContents] = useState(deckList[selectedDeckIndex]);

  useEffect(() => {
    localStorage.setItem('conan-cardgame-deckList', JSON.stringify(deckList));
  }, [deckList]);

  useEffect(() => {
    localStorage.setItem('conan-cardgame-deckContents', JSON.stringify(deckContents));
  }, [deckContents]);

  useEffect(() => {
    localStorage.setItem('conan-cardgame-selectedDeckIndex', selectedDeckIndex);
  }, [selectedDeckIndex]);


  const [open, setOpen] = useState(false);

  const handleOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const windowWidth = useWindowWidth();
  const isMobile = windowWidth <= 960;

  const [isEditingDeckVisible, setIsEditingDeckVisible] = useState(false);











  return (
    <div>
      <HideOnScrollAppBar>
        <SortAndFilter
          sortField={sortField}
          setSortField={setSortField}
          sortOrder={sortOrder}
          setSortOrder={setSortOrder}
          filterText={filterText}
          setFilterText={setFilterText}
          filterProduct={filterProduct}
          setFilterProduct={setFilterProduct}
          filterColor={filterColor}
          setFilterColor={setFilterColor}
          filterType={filterType}
          setFilterType={setFilterType}
          filterRarity={filterRarity}
          setFilterRarity={setFilterRarity}
        />
      </HideOnScrollAppBar>

      <Box sx={{ position: 'fixed', bottom: 16, right: 16, zIndex: 9 }}>
        <Fab color="primary" aria-label="add" onClick={() => setIsEditingDeckVisible(!isEditingDeckVisible)} sx={{ boxShadow: '0px 0px 5px 5px rgba(0, 0, 0, 1)' }}>
          {isEditingDeckVisible ? 'hide' : 'deck'}
        </Fab>
      </Box>

      {!isMobile &&
        <Box sx={{
          // marginLeft: isEditingDeckVisible ? '50%' : '0',
          //  width: isEditingDeckVisible ? '50%' : '100%' 
        }}>
          <CardOfList
            displayedCardList={displayedCardList}
            deckList={deckList}
            setDeckList={setDeckList}
            deckContents={deckContents}
            setDeckContents={setDeckContents}
            // viewWidth={isEditingDeckVisible ? windowWidth / 2 : windowWidth}
            viewWidth={windowWidth}
          />
          <Drawer anchor="right" open={isEditingDeckVisible} onClose={() => setIsEditingDeckVisible(false)}>
            <EditingDeck
              deckList={deckList}
              setDeckList={setDeckList}
              selectedDeckIndex={selectedDeckIndex}
              setSelectedDeckIndex={setSelectedDeckIndex}
              deckContents={deckContents}
              setDeckContents={setDeckContents}
              viewWidth={ Math.min( windowWidth , 960)}
            />
          </Drawer>
        </Box>
      }


      {isMobile && (
        <>
          <CardOfList
            displayedCardList={displayedCardList}
            deckList={deckList}
            setDeckList={setDeckList}
            deckContents={deckContents}
            setDeckContents={setDeckContents}
            viewWidth={windowWidth}
          />

          <Drawer anchor="bottom" open={isEditingDeckVisible} onClose={() => setIsEditingDeckVisible(false)}>
            <EditingDeck
              deckList={deckList}
              setDeckList={setDeckList}
              selectedDeckIndex={selectedDeckIndex}
              setSelectedDeckIndex={setSelectedDeckIndex}
              deckContents={deckContents}
              setDeckContents={setDeckContents}
              viewWidth={windowWidth}
            />
          </Drawer>
        </>
      )}




    </div>
  );
};


